@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Variables */
/* Common */
body, html {
  overflow-x: hidden; }
  body *, html * {
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box; }
  body p, body h1, body h2, body h3, body h4, body h5, html p, html h1, html h2, html h3, html h4, html h5 {
    margin-top: 0;
    margin-bottom: 0.75em; }

.row {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }
  .row.space-between {
    justify-content: space-between; }
  .row.align-top {
    align-items: flex-start; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

input.custom-input {
  border: 2px solid #DEDEE0;
  border-radius: 5px;
  padding: 0 5px;
  line-height: 1.75em;
  font-size: 18px;
  color: #6F6F6F;
  outline: none;
  -webkit-transition: all ease-in 150ms;
  transition: all ease-in 150ms; }
  input.custom-input::-webkit-input-placeholder {
    /* Edge */
    color: #D8D8D8; }
  input.custom-input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #D8D8D8; }
  input.custom-input::-moz-placeholder {
    color: #D8D8D8; }
  input.custom-input::-ms-input-placeholder {
    color: #D8D8D8; }
  input.custom-input::placeholder {
    color: #D8D8D8; }
  input.custom-input:focus {
    border-color: #784CC0;
    -webkit-transition: all ease-out 150ms;
    transition: all ease-out 150ms; }
  input.custom-input.input-block {
    width: 100%; }
  input.custom-input.input-large {
    padding: 0 10px;
    font-size: 24px;
    line-height: 2em; }

.container {
  width: 1200px;
  max-width: 100%;
  padding: 0 15px;
  margin: auto; }

.cta {
  background-color: transparent;
  border: none;
  border-radius: 4px;
  cursor: pointer; }

/* Main Style */
.section-page-header {
  margin: 40px 0 20px; }
  .section-page-header .page-navigation .nav-item {
    color: #784CC0;
    font-size: 18px;
    line-height: 18px;
    background: transparent;
    border: none;
    padding: 0;
    position: relative;
    display: inline-block;
    cursor: pointer;
    text-decoration: none; }
    .section-page-header .page-navigation .nav-item:before {
      content: '';
      display: block;
      width: 12px;
      height: 12px;
      position: absolute;
      -webkit-transform-origin: center;
              transform-origin: center;
      border: 2px solid #784CC0;
      top: 1px; }
    .section-page-header .page-navigation .nav-item.back {
      padding-left: 20px; }
      .section-page-header .page-navigation .nav-item.back:before {
        border-bottom: none;
        border-right: none;
        border-top-left-radius: 2px;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        left: 0; }
  .section-page-header .page-title {
    font-weight: 900;
    font-size: 60px;
    color: #B2B2B2;
    margin: 0;
    padding: 20px 0; }
    .section-page-header .page-title.style-2 {
      font-weight: 400;
      font-size: 42px;
      color: #252525; }
  .section-page-header .add-item {
    background-color: #784CC0;
    color: #FFF;
    padding: 0 15px 0 50px;
    line-height: 40px;
    font-size: 20px;
    position: relative;
    text-decoration: none; }
    .section-page-header .add-item:before {
      content: '+';
      position: absolute;
      width: 50px;
      height: 40px;
      top: 0;
      left: 0;
      text-align: center;
      font-weight: 400;
      font-size: 32px; }

.image-list {
  margin: 0 -15px; }

/* Variables */
.image-item {
  flex: 0 0 33%;
  width: 33%;
  padding: 0 15px;
  margin-bottom: 20px; }
  .image-item .image-thumbnail {
    display: flex;
    align-items: center;
    padding-bottom: 30px;
    cursor: pointer;
    position: relative; }
    .image-item .image-thumbnail .main-image {
      height: 200px;
      width: 100%;
      border-radius: 5px;
      overflow: hidden;
      position: relative;
      z-index: 2; }
      .image-item .image-thumbnail .main-image .image-holder {
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 200px;
        -webkit-transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
        transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94); }
    .image-item .image-thumbnail .image-shadow {
      position: absolute;
      top: 0;
      width: 80%;
      left: 10%;
      height: 200px;
      box-shadow: 0 8px 15px rgba(0, 0, 0, 0.35); }
    .image-item .image-thumbnail:hover .image-holder {
      -webkit-transform: scale(1.15);
      transform: scale(1.15);
      -webkit-transition: all 900ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
      transition: all 900ms cubic-bezier(0.25, 0.46, 0.45, 0.94); }
  .image-item .image-context .author, .image-item .image-context .date-created {
    color: #6F6F6F;
    font-size: 16px; }
  .image-item .image-context .title {
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    height: 38px;
    overflow: hidden;
    margin-bottom: 0.25em;
    max-width: 100%; }
  .image-item .image-context .description {
    color: #6F6F6F;
    font-size: 17px;
    line-height: 26px;
    height: 56px;
    overflow: hidden; }
  .image-item .image-context .image-cta li {
    margin-right: 6px; }
  .image-item .image-context .image-cta button {
    border: 2px solid #EDEDEF;
    width: 50px;
    height: 50px; }
    .image-item .image-context .image-cta button svg.inactive > * {
      fill: #EDEDEF !important; }
    .image-item .image-context .image-cta button svg.active-heart > * {
      fill: #E54D42; }
    .image-item .image-context .image-cta button:hover {
      border: 2px solid #6F6F6F; }
      .image-item .image-context .image-cta button:hover svg.inactive > * {
        fill: #6F6F6F !important; }

.section-search-form {
  margin-bottom: 40px; }

